  .review-card {
    --cardPadding: 50px 55px;
    font-size: 20px;
    font-weight: 300;
  }
}
@media (min-width: 1280px) {
  .review-card {
    --cardPadding: 40px 60px 10px;
    align-items: stretch;
    box-shadow: none;
    cursor: pointer;
    font-size: 18px;
  }

  .review-card__author {
    padding-right: 10px;
    justify-content: right;
  }

  .review-card__hr {
    width: 33px;
  }

  .review-card:hover {
    box-shadow: 0 4px 7px rgba(7, 55, 98, 0.25);
  }
}

@media (min-width: 1440px) {
  .review-card {
    --cardPadding: 45px 50px 10px;
    font-size: 22px;
  }

  .review-card__author {
    padding-right: 30px;
    justify-content: right;
  }
}

.review {
  padding: 58px 15px 14px;
  display: flex;
  flex-direction: column;
  @media (min-width: 320px) and (max-width: 599.99px) {
    padding-top: 40px !important;
  }
  @media (max-width: 600px) {
    padding: 50px 20px 50px;
  }
  @media (min-width: 600px) and (max-width: 1279.99px) {
    padding-top: 30px !important;
  }
}

.review__section {
  padding: 0;
  width: 100%;
  position: relative;
}

.review__title {
  margin: 0 0 0 5px;
  font-family: Oblik;
  font-weight: 900;
  font-size: 20px;
  line-height: 50px;
  letter-spacing: 0.1em;
  white-space: nowrap;
}

.review__buttons {
  display: none;
}

.custom-slider {
  padding: 10px 0;
}

.custom-slide {
  padding: 10px 6px;
}

.swiper-pagination {
  height: 40px;
  display: flex;
  grid-gap: 8px;
  gap: 8px;
  align-items: center;
  justify-content: center;
}

.bulletClass {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #c4c4c4;
}

.bulletActiveClass {
  background-color: #073762;
}

@media (min-width: 1280px) {
  .review {
    margin-bottom: 64px;
  }

  .review__section {
    display: flex;
    grid-gap: 7%;
    gap: 7%;
  }

  .review__title {
    margin: 6.5rem 0 0 -15px;
    width: 36px;
    height: -moz-fit-content;
    height: fit-content;
    font-size: 24px;
    line-height: 1.2;
    transform: rotate(-90deg);
  }

  .custom-slider {
    padding: 10px 1.9% 10px 0;
  }

  .review__buttons {
    display: flex;
    justify-content: flex-end;
    grid-gap: 1px;
    gap: 1px;
  }

  .review__button {
    width: 34px;
    height: 24px;
    cursor: pointer;
    border: none;
    padding: 0 12.5px;
    background: none;
    background: url(/BlueHouse/static/media/shevron-right.12f3eadb.svg) center / auto
      no-repeat;
  }

  .button-prev {
    transform: rotate(180deg);
  }

  .swiper-pagination {
    display: none;
  }
}

